.menuheader{
    margin-top: 80px;
    width: 100%;
    height: 40px;
    /* background-color: white; */
}
.menuheader ul{
    position: absolute;
    font-family: Salsa;
    list-style:none;
    margin: 0;
    padding-left: 116px;
    /* background-color: white; */
}
.menuheader > ul{
    font-family: Salsa;
    display: flex;
    margin: 0 50px;
    position: relative;
    /* background-color: white; */
}
.menuheader >ul > li > span{
    font-family: Salsa;
    display: block;
    color: #A78A8A;
    line-height:40px ;
    cursor: pointer;
    padding: 0 20px;
    font-size: 16px;
    /* background-color: white; */
}
.menuheader >ul > li>span:hover{
    color:black;
    font-family: Salsa;
}
.menuheader > ul > li > ul{
    position:absolute;
    /* background-color: white; */
    font-family: Salsa;
    left: 0;
    right: 0;
    display: none;
    /* border: 1px solid #cecece; */
    z-index: 1;
}
.menuheader > ul > li:hover ul{
    display: block;
    background-color: transparent;
    /* flex-direction: column; */
    font-family: Salsa;
    
}
.menuheader >ul >li >ul>li{
    /* position: relative; */
    margin: 0 20px;
    font-family: Salsa;
    float: left;
    min-width: 150px;
}
.menuheader > ul >li >ul >li >a{
    /* position: relative; */
    font-family: Salsa;
    display: block;
}
.menuheader >ul>li>ul>li a{
    padding: 3px 0;
    font-family: Salsa;
    display: block;
    font-size: 14px;
    text-decoration: none;
    color: black;
}
.menuheader >ul>li>ul>li a:hover{
    font-family: Salsa;
}
.menuheader >ul >li >ul>li>ul{
    /* position: relative; */
    /* margin: 0 20px; */
    margin-left: 20px;
    font-family: Salsa;
    float: left;
    display: block;
   
    /* min-width: 150px; */
}
